/*
 *
 * CodePage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import * as ducks from 'containers/Console/ducks';
import ConsolePageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import { AddIcon, CodeIcon, RolesIcon } from 'components/EntityIcons';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import LearnMoreLink from 'components/LearnMoreLink';
import { cb } from 'cb-utils/cbLib';
import { Button } from 'reactstrap';
import { getFolder } from 'components/SearchableFileSystem';
import FailedRunsAlert from 'components/FailedRunsAlert';
import CardTable from 'components/CardTableForEntities';
import CodeCard from 'components/CodeCard';
import { RouteComponentProps } from 'react-router';
import CbDropDown from 'components/CbDropDown';
import { GearDropDown } from 'containers/EdgesPage';
import { createStructuredSelector } from 'reselect';

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.name} />
        </b>
      ),
      isSortable: true,
      sortKey: 'name',
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.Triggers} />
        </b>
      ),
      isSortable: true,
      sortKey: 'triggers',
    },
    {
      component: (
        <b>
          <FormattedMessage {...messages.Timers} />
        </b>
      ),
      isSortable: true,
      sortKey: 'timers',
    },
  ];
};

type MasterProps = IDispatchToProps & IStateToProps & RouteComponentProps<{}>;

export class CodePage extends React.PureComponent<MasterProps> {
  state = {
    selected: '',
  };
  componentWillMount() {
    const query = cb.Query().setPage(25, 1);
    this.props.fetchFailedRuns({ query });
    this.props.fetchFailedRunsCount({ query });
    this.props.fetchLibraries();
    this.props.fetchServices();
    this.props.fetchTimers();
    this.props.fetchTriggers();
  }

  renderAddButton = ({ size = 'md' }) => (
    <Button
      size={size}
      onClick={() =>
        this.props.createShowBaseModal({
          component: 'NewCodeModal',
          props: { namespace: this.state.selected },
        })
      }
      data-cy="add-code"
      color="primary"
    >
      <AddIcon /> <FormattedMessage {...messages.addCode} />
    </Button>
  );

  render() {
    const currentFolder = getFolder(this.props.location.search);
    const title = (
      <ConsolePageHeader
        header={
          <div>
            <CodeIcon /> <FormattedMessage {...messages.header} />{' '}
            <CbDropDown
              triggerEl={
                <GearDropDown data-cy="open-device-settings-dropdown" />
              }
              items={[
                {
                  displayName: (
                    <div>
                      <RolesIcon /> <FormattedMessage {...messages.editRoles} />
                    </div>
                  ),
                  'data-test': 'edit-services-overall-roles-btn',
                  onClick: () =>
                    this.props.createShowBaseModal({
                      component: 'EditRolesModal',
                      props: {
                        assetClass: 'services',
                      },
                    }),
                },
              ]}
            />
          </div>
        }
        actionButton={this.renderAddButton}
        subHeader={
          <FailedRunsAlert
            createShowBaseModal={this.props.createShowBaseModal}
            count={this.props.failedRunsCount}
          />
        }
      />
    );

    let list = this.props.allCodeArray;
    if (currentFolder) {
      list = list.filter((lib) => {
        const inFolder = lib.namespace.match(
          new RegExp('^' + currentFolder + '/', 'g'),
        );
        return inFolder;
      });
    }

    return (
      <ConsolePage
        subheader={title}
        isFetchingAssets={
          this.props.isFetchingServices || this.props.isFetchingLibraries
        }
        zeroState={{
          show:
            this.props.totalServices === 0 && this.props.totalLibraries === 0,
          title: messages.header,
          icon: <CodeIcon />,
          addButton: this.renderAddButton({ size: 'lg' }),
          info: messages.zeroStateInfo,
          moreInfo: (
            <LearnMoreLink url="https://docs.clearblade.com/v/3/1-platform_concepts/Code/" />
          ),
        }}
      >
        <Helmet
          title="ClearBlade - Code"
          meta={[{ name: 'description', content: 'Description of CodePage' }]}
        />
        {list && (
          <CardTable
            defaultSortKey="name"
            headerColumns={createColumnsForCardTable()}
            list={list}
            entityCard={CodeCard}
            cardProp="code"
          />
        )}
      </ConsolePage>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  isFetchingServices: ducks.serviceSelectors.fetchAll.isLoading,
  totalServices: ducks.serviceSelectors.entitiesCount,
  isFetchingLibraries: ducks.librarySelectors.fetchAll.isLoading,
  totalLibraries: ducks.librarySelectors.customLibrariesCount,
  libraries: ducks.librarySelectors.selectCustomLibraries(),
  failedRunsCount: ducks.failedRuns.selectors.filteredCount(),
  allCodeArray: ducks.services.selectors.allCode(),
});
type IStateToProps = ReturnType<typeof mapStateToProps>;

const mapDispatchToProps = {
  fetchFailedRuns: ducks.failedRuns.actions.fetchAllQuery.request,
  fetchFailedRunsCount: ducks.failedRuns.actions.fetchCount.request,
  fetchLibraries: ducks.libraries.actions.fetchAll.request,
  fetchServices: ducks.services.actions.fetchAll.request,
  fetchTimers: ducks.timers.actions.fetchAll.request,
  fetchTriggers: ducks.triggers.actions.fetchAll.request,
  createShowBaseModal,
};

type IDispatchToProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(CodePage);



