/**
 *
 * CodeCard
 *
 */

import React, { SFC } from 'react';
import {
  ServiceModelWTriggers,
  LibraryModel,
} from 'cb-utils/console-entity-models';
import { withRouter } from 'react-router';
import HoverListItem from 'components/HoverListItem';
import LinkWrapper from 'components/LinkWithoutDecoration';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { LibraryIcon, CodeIcon } from '../EntityIcons';
import { detailUrls } from 'cb-utils/getUrls';
import { SuccinctTimer } from 'containers/EditTimers';
import { SuccinctTrigger } from 'containers/NewTrigger/SuccinctTrigger';
import { Col, Row } from 'reactstrap';

interface CodeCardProps extends SystemRouteProps<{}> {
  code: ServiceModelWTriggers;
}

export const CodeCard: SFC<CodeCardProps> = ({ code, match, ...props }) => {
  const displayName = code.name;
  const isLibrary = !!(code as LibraryModel).visibility;
  return (
    <HoverListItem>
      <LinkWrapper
        to={
          isLibrary
            ? detailUrls.libraries(displayName)
            : detailUrls.services(displayName)
        }
      >
        <Row className="p-3">
          <Col sm="12">
            <small className="text-muted">{code.namespace}</small>
          </Col>
          <Col sm="4">
            <div className="c-pointer" data-cy="Code-link">
              {isLibrary ? <LibraryIcon /> : <CodeIcon />} {displayName}
            </div>
            <div />
          </Col>
          <Col sm="4" className="text-muted">
            {code.triggers &&
              code.triggers.map((trigger) => (
                <SuccinctTrigger key={trigger.name} {...trigger} />
              ))}
          </Col>
          <Col sm="4" className="text-muted">
            {code.timers &&
              code.timers.map((timer) => (
                <SuccinctTimer key={timer.name} {...timer} />
              ))}
          </Col>
          <Col sm="12" className="text-muted">
            {(code as LibraryModel).description}
          </Col>
        </Row>
      </LinkWrapper>
    </HoverListItem>
  );
};

export default withRouter(CodeCard);



